import React, { useRef, useState, useEffect } from 'react';
import ReactPlayer from 'react-player';
import useNuiMessage from '../hooks/useNuiMessage';

const Player = () => {
    const player = useRef(null);
    const iframeRef = useRef(null);
    const [isVisible, setIsVisible] = useState(true);
    const [url, setUrl] = useState('');
    const [resourceName, setResourceName] = useState('hypnonema');
    const [posterUrl, setPosterUrl] = useState('https://i.imgur.com/dPaIjEW.jpg');
    const [screenName, setScreenName] = useState('');
    const [playing, setPlaying] = useState(false);
    const [volume, setVolume] = useState(0.0);
    const [muted, setMuted] = useState(false); // Start muted to handle autoplay policies
    const [repeat, setRepeat] = useState(false);
    const [iframeKey, setIframeKey] = useState(0);
    const [iframeError, setIframeError] = useState(false);

    useEffect(() => {
        const handleVolumeChange = () => {
            if (iframeRef.current && url.includes('/user/')) {
                try {
                    const iframeDoc = iframeRef.current.contentDocument || iframeRef.current.contentWindow.document;
                    const mediaElement = iframeDoc.querySelector('audio, video');

                    if (volume === 0) {
                        setIsVisible(false);
                    } else if (!isVisible) {
                        console.log("set visible")
                        setIsVisible(true);
                        setIframeKey(prevKey => prevKey + 1);
                    }

                    if (!isVisible) {
                        return null;
                    }
                } catch (error) {
                    console.error('Error accessing iframe content:', error);
                }
            }
        };

        handleVolumeChange();
    }, [volume, muted, url, isVisible]);

    const onVolume = ({ payload }) => {
        setVolume(parseFloat(payload));
    };

    const onMute = ({ payload }) => {
        setMuted(payload);
    };

    const onPlay = ({ payload }) => {
        setUrl(payload);
        setPlaying(true);
        setIframeError(false); // Reset iframe error state
    };

    const onStop = () => {
        setPlaying(false);
        setUrl('');
    };

    const onInit = ({ payload }) => {
        const { resourceName, screenName, posterUrl } = payload;
        setScreenName(screenName);
        setResourceName(resourceName);
        setPosterUrl(posterUrl);
    };

    const onPause = () => {
        setPlaying(false);
    };

    const onResume = () => {
        setPlaying(true);
    };

    const onSeek = ({ payload }) => {
        player.current.seekTo(payload, 'seconds');
    };

    const onRepeat = ({ payload }) => {
        setRepeat(payload);
    };

    const onSynchronizeState = ({ payload }) => {
        const { url, paused, currentTime, repeat } = payload;
        setUrl(url);
        if (!paused) {
            setPlaying(true);
        }
        player.current.seekTo(currentTime, 'seconds');
        setRepeat(repeat);
    };

    const onReady = () => {
        if (player.current.player.player.constructor.name === 'Twitch') {
            const internalPlayer = player.current.getInternalPlayer();
            const button = internalPlayer._iframe.contentWindow.document.querySelector('button[data-a-target="player-overlay-mature-accept"]');
            if (button) {
                button.click();
            }
        }
    };

    const onStart = () => {
        sendDuiResponse('playbackStart', { screenName, date: new Date().toISOString() }).then(() => { });
    };

    const onEnded = () => {
        setTimeout(() => {
            sendDuiResponse('playbackEnded', { screenName }).then(() => {
            });
        }, 2500);
    };

    const onDuration = (duration) => {
        sendDuiResponse('updateStateDuration', { screenName, duration }).then(() => {
        });
    };

    const onError = (e) => {
        console.error('ReactPlayer Error:', e);
        if (e && e.code === 150) {
            console.log('This video cannot be played due to content restrictions.');
        } else {
            console.log('An error occurred with the video player.');
        }
        // Reset the player state to allow for retries or alternative actions
        setPlaying(false);
        setUrl('');
    };

    useNuiMessage('synchronizeState', onSynchronizeState);
    useNuiMessage('volume', onVolume);
    useNuiMessage('mute', onMute);
    useNuiMessage('repeat', onRepeat);
    useNuiMessage('seek', onSeek);
    useNuiMessage('resume', onResume);
    useNuiMessage('play', onPlay);
    useNuiMessage('stop', onStop);
    useNuiMessage('init', onInit);
    useNuiMessage('pause', onPause);

    const sendDuiResponse = (nuiCallback, body) => {
        const url = new URL(nuiCallback, `${window.location.protocol}${resourceName}`).toString();
        return fetch(url, {
            headers: { 'content-type': 'application/json; charset=UTF-8' },
            method: 'POST',
            body: JSON.stringify(body)
        }).catch(error => console.log(error));
    };

    const handleIframeError = () => {
        setIframeError(true);
    };

    return (
        <div style={{ width: '100%', height: '100%' }}>
            {!playing &&
                <div id='posterImg'>
                    <img src={posterUrl} alt='' />
                </div>
            }
            <div className='player-wrapper' style={{ width: '100%', height: '100%' }}>
                {url.includes('/user/') ? (
                    iframeError ? (
                        <div className='error-message'>
                            <p>There was an error loading the content. Please try again later.</p>
                        </div>
                    ) : (
                        <iframe
                            key={iframeKey}
                            ref={iframeRef}
                            src={url}
                            onError={handleIframeError}
                            style={{
                                width: '100%',
                                height: '100%',
                                border: 'none',
                                position: 'absolute',
                                top: 0,
                                left: 0,
                                right: 0,
                                bottom: 0
                            }}
                        />
                    )
                ) : (
                    <ReactPlayer
                        ref={player}
                        className='react-player'
                        url={url}
                        pip={false}
                        playing={playing}
                        controls={false}
                        loop={repeat}
                        playbackRate={1.0}
                        volume={volume}
                        muted={muted}
                        onDuration={onDuration}
                        onReady={onReady}
                        onEnded={onEnded}
                        onStart={onStart}
                        onError={onError}
                        width='100%'
                        height='100%'
                    />
                )}
            </div>
        </div>
    );
};

export default Player;
